<script>
	import { onMount } from 'svelte';
	import { app } from '@/stores/app';

	import { products, filteredProducts } from '@/stores/products';
	import { productFilter } from '@/stores/product-filter';

	const { __ } = window.gkLicenses;
	const { permissions } = $app;

	const PRODUCT_FILTER_ALL = 'all';
	const PRODUCT_FILTER_UNLICENSED = 'unlicensed';
	const PRODUCT_FILTER_UPDATE_AVAILABLE = 'update-available';
	const PRODUCT_FILTER_ACTIVE = 'active';
	const PRODUCT_FILTER_INACTIVE = 'inactive';
	const PRODUCT_FILTER_NOT_INSTALLED = 'not-installed';
	const PRODUCT_FILTER_CUSTOM = 'custom';

	let activeFilter = PRODUCT_FILTER_ALL;
	let previousFilter = PRODUCT_FILTER_ALL;

	let customFilter = '';
	let debounceTimer;

	let productCount = {};

	productFilter.set( { activeFilter, customFilter } );

	/**
	 * Sets focus on the element when it's added to DOM.
	 *
	 * @since 1.0.0
	 */
	function focusElement( el ) {
		el.focus();
	}

	/**
	 * Filters products.
	 *
	 * @since 1.0.0
	 *
	 * @param {string} filter
	 *
	 * @returns {Object}
	 */
	function filterProducts( filter = activeFilter ) {
		if ( filter === PRODUCT_FILTER_ALL ) {
			return products.get();
		}

		if ( filter === PRODUCT_FILTER_CUSTOM ) {
			return filterByKeyword( customFilter );
		}

		return filterByStatus( filter );
	}

	/**
	 * Filters products using a keyword (matches product title and excerpt).
	 *
	 * @since 1.0.0
	 *
	 * @param {string} keyword
	 *
	 * @returns {Object}
	 */
	function filterByKeyword( keyword ) {
		if ( !keyword ) {
			return products.get();
		}

		return products.get().map( data => {
			let filterRegex;
			let products;

			if ( keyword.match( new RegExp( '^id:', 'ig' ) ) ) {
				const id = ~~keyword.replace( new RegExp( '^id:', 'ig' ), '' );

				products = data.products.filter( product => product.id === id );

				filterRegex = products.length ? new RegExp( `(${products[0].title})`, 'ig' ) : new RegExp( `(${keyword})`, 'ig' );
			} else {
				filterRegex = new RegExp( `(${keyword})`, 'ig' );

				products = data.products.filter( product => product.title.match( filterRegex ) || product.excerpt.match( filterRegex ) );
			}

			if ( products.length ) {
				products = products.map( product => {
					product.title = product.title.replace( filterRegex, '<span class="bg-yellow-200">$1</span>' );

					product.excerpt = product.excerpt.replace( filterRegex, '<span class="bg-yellow-200">$1</span>' );

					return product;
				} );
			}

			return products.length ? { ...data, products } : null;
		} ).filter( Boolean );
	}

	/**
	 * Filters products by their status (activated, installed).
	 *
	 * @since 1.0.0
	 *
	 * @param {string} status
	 *
	 * @returns {Object}
	 */
	function filterByStatus( status ) {
		return products.get().map( data => {
			const products = data.products.filter( product => {
				switch ( status ) {
					case PRODUCT_FILTER_UNLICENSED:
						// This has to match the backend logic; see GravityKit\Foundation\Licenses\LicenseManager\update_submenu_badge_count().
						return product.active && !product.licenses.length;
					case PRODUCT_FILTER_UPDATE_AVAILABLE:
						// This has to match the backend logic; see GravityKit\Foundation\Licenses\ProductManager\update_submenu_badge_count().
						return product.update_available && product.active && product.licenses.length;
					case PRODUCT_FILTER_ACTIVE:
						return product.installed && product.active;
					case PRODUCT_FILTER_INACTIVE:
						return product.installed && !product.active;
					case PRODUCT_FILTER_NOT_INSTALLED:
						return !product.installed;
					default:
						return true;
				}
			} );

			return products.length ? { ...data, products } : null;
		} ).filter( Boolean );
	}

	/**
	 * Updates product count for each filter category.
	 *
	 * @since 1.0.0
	 *
	 * @returns {Object}
	 */
	function updateProductsCount() {
		[ PRODUCT_FILTER_ALL, PRODUCT_FILTER_UNLICENSED, PRODUCT_FILTER_UPDATE_AVAILABLE, PRODUCT_FILTER_ACTIVE, PRODUCT_FILTER_INACTIVE, PRODUCT_FILTER_NOT_INSTALLED ].forEach( filter => {
			productCount[ filter ] = filterProducts( filter ).reduce( ( count, data ) => count + data.products.length, 0 );
		} );
	}

	/**
	 * Sets product filter.
	 *
	 * @since 1.0.0
	 *
	 * @param {string} filter
	 */
	function setFilter( filter ) {
		previousFilter = activeFilter;

		activeFilter = filter;

		updateUrlParameters();

		filteredProducts.set( filterProducts() );

		productFilter.set( { activeFilter, customFilter } );
	}

	/**
	 * Processes custom filter key input, including especial key presses.
	 *
	 * @since 1.0.0
	 *
	 * @param {Object} obj
	 * @param {Object} obj.code Pressed keyboard code.
	 * @param {Object} obj.value Search keyword.
	 */
	function handleCustomFilterInputChange( { code, value } ) {
		if ( code === 'Escape' ) {
			activeFilter = previousFilter;

			updateUrlParameters();

			return filteredProducts.set( filterProducts() );
		}

		clearTimeout( debounceTimer );

		customFilter = value;

		debounceTimer = setTimeout( () => {
			updateUrlParameters();

			filteredProducts.set( filterProducts() );
		}, 250 );
	}

	/**
	 * Updates URL query string based on configured product filter.
	 *
	 * @since 1.0.0
	 */
	function updateUrlParameters() {
		const urlParams = new URLSearchParams( window.location.search );

		urlParams.set( 'filter', activeFilter );

		if ( activeFilter === PRODUCT_FILTER_CUSTOM && '' !== customFilter ) {
			urlParams.set( 'search', customFilter );
		} else {
			urlParams.delete( 'search' );
		}

		if ( activeFilter === PRODUCT_FILTER_ALL ) {
			urlParams.delete( 'filter' );
		}

		const url = !urlParams.toString().length ? window.location.href.split( '?' )[ 0 ] : `?${ urlParams.toString() }`;

		window.history.pushState( {}, document.title, url );
	}

	/**
	 * Processes URL query string and sets product filter.
	 *
	 * @since 1.0.0
	 */
	function processUrlQueryString() {
		const urlParams = new URLSearchParams( window.location.search );
		const filter = urlParams.get( 'filter' );
		const filterKeyword = urlParams.get( 'search' );
		const availableFilters = new Set( [
			PRODUCT_FILTER_ALL,
			PRODUCT_FILTER_UNLICENSED,
			PRODUCT_FILTER_UPDATE_AVAILABLE,
			PRODUCT_FILTER_ACTIVE,
			PRODUCT_FILTER_INACTIVE,
			PRODUCT_FILTER_NOT_INSTALLED,
			PRODUCT_FILTER_CUSTOM
		] );

		if ( availableFilters.has( filter ) ) {
			activeFilter = filter;
		} else {
			activeFilter = PRODUCT_FILTER_ALL;
		}

		if ( filter === PRODUCT_FILTER_CUSTOM ) {
			customFilter = filterKeyword ?? '';
		}

		filteredProducts.set( filterProducts() );
	}

	onMount( () => {
		processUrlQueryString();

		window.addEventListener( 'popstate', processUrlQueryString );

		products.subscribe( () => {
			filteredProducts.set( filterProducts() );

			updateProductsCount();
		} );

		productFilter.subscribe( () => {
			if ( !$productFilter.activeFilter ) {
				setFilter( PRODUCT_FILTER_ALL );
			}
		} );
	} );
</script>

{#if $app.loader}
	<div class="space-y-4">
		<div class="hidden sm:block">
			<div class="bg-white pl-5 border rounded-md mt-6 shadow-sm">
				<nav class="-mb-px flex items-center space-x-5 md:space-x-2 lg:space-x-6" aria-label="Tabs">
					{#each [ ...Array( 4 ).keys() ] as placeholder}
						<div class="h-4 w-[10%] rounded-sm bg-gray-300 motion-safe:animate-pulse mb-4 mt-4"></div>
					{/each}
					<div class="relative h-full">
						<svg class="text-gray-300 absolute top-1/2 left-2.5 transform -translate-y-1/2" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
							<path fill="currentColor" d="M12.7 11.3C13.6 10.1 14.1 8.7 14.1 7.1C14.1 3.2 11 0 7.1 0C3.2 0 0 3.2 0 7.1C0 11 3.2 14.2 7.1 14.2C8.7 14.2 10.2 13.7 11.3 12.8L14.3 15.8C14.5 16 14.8 16.1 15 16.1C15.2 16.1 15.5 16 15.7 15.8C16.1 15.4 16.1 14.8 15.7 14.4L12.7 11.3ZM7.1 12.1C4.3 12.1 2 9.9 2 7.1C2 4.3 4.3 2 7.1 2C9.9 2 12.2 4.3 12.2 7.1C12.2 9.9 9.9 12.1 7.1 12.1Z"></path>
						</svg>
					</div>
				</nav>
			</div>
		</div>
	</div>
{:else}
	<div class="space-y-4">
		<div class="sm:hidden">
			<label for="tabs" class="sr-only">
				{ __( 'Select a product filter or search or products', 'gk-foundation' ) }
			</label>
			<select
				id="tabs"
				class="block w-full max-w-none py-2 text-base border-gray-300 focus:outline-none focus:ring-gray-900 focus:border-gray-900 sm:text-sm rounded-md"
				value="{activeFilter}"
				on:change={({target: {value}}) => setFilter(value) }
			>
				<optgroup label="{ __( 'Filters', 'gk-foundation' ) }">
					<option value="{PRODUCT_FILTER_ALL}">
						{ __( 'All', 'gk-foundation' ) } ({productCount[ PRODUCT_FILTER_ALL ]})
					</option>
					{#if permissions.manage_licenses && productCount[ PRODUCT_FILTER_UNLICENSED ]}
						<option value="{PRODUCT_FILTER_UNLICENSED}">
							{ __( 'Unlicensed', 'gk-foundation' ) } ({productCount[ PRODUCT_FILTER_UNLICENSED ]})
						</option>
					{/if}
					{#if permissions.install_products && productCount[ PRODUCT_FILTER_UPDATE_AVAILABLE ]}
						<option value="{PRODUCT_FILTER_UPDATE_AVAILABLE}">
							{ __( 'Update Available', 'gk-foundation' ) } ({productCount[ PRODUCT_FILTER_UPDATE_AVAILABLE ]})
						</option>
					{/if}
					<option value="{PRODUCT_FILTER_ACTIVE}">
						{ __( 'Active', 'gk-foundation' ) } ({productCount[ PRODUCT_FILTER_ACTIVE ]})
					</option>
					<option value="{PRODUCT_FILTER_INACTIVE}">
						{ __( 'Inactive', 'gk-foundation' ) } ({productCount[ PRODUCT_FILTER_INACTIVE ]})
					</option>
					<option value="{PRODUCT_FILTER_NOT_INSTALLED}">
						{ __( 'Not Installed', 'gk-foundation' ) } ({productCount[ PRODUCT_FILTER_NOT_INSTALLED ]})
					</option>
				</optgroup>
				<optgroup label="{ __( 'Search', 'gk-foundation' ) }">
					<option value="{PRODUCT_FILTER_CUSTOM}">
						{ __( 'Search Products', 'gk-foundation' ) }
					</option>
				</optgroup>
			</select>
		</div>
		{#if activeFilter === PRODUCT_FILTER_CUSTOM}
			<div class="relative w-full bg-white border border-gray-300 rounded-md focus-within:outline-none focus-within:ring-2 focus-within:ring-gray-900 sm:hidden">
				<svg class="absolute top-1/2 left-2.5 transform -translate-y-1/2" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
					<title>
						{ __( 'Search Products', 'gk-foundation' ) }
					</title>
					<path d="M12.7 11.3C13.6 10.1 14.1 8.7 14.1 7.1C14.1 3.2 11 0 7.1 0C3.2 0 0 3.2 0 7.1C0 11 3.2 14.2 7.1 14.2C8.7 14.2 10.2 13.7 11.3 12.8L14.3 15.8C14.5 16 14.8 16.1 15 16.1C15.2 16.1 15.5 16 15.7 15.8C16.1 15.4 16.1 14.8 15.7 14.4L12.7 11.3ZM7.1 12.1C4.3 12.1 2 9.9 2 7.1C2 4.3 4.3 2 7.1 2C9.9 2 12.2 4.3 12.2 7.1C12.2 9.9 9.9 12.1 7.1 12.1Z" fill="currentColor"></path>
				</svg>
				<input
					aria-label="{ __( 'Search all GravityKit products using a keyword', 'gk-foundation' ) }"
					type="search"
					value={customFilter}
					use:focusElement
					on:keyup={ ( { code, target: {value} } ) => handleCustomFilterInputChange({code, value}) }
					class="w-full pl-9 bg-transparent border-none outline-none shadow-none sm-md:text-sm"
					placeholder="{ __( 'Search products by title or description', 'gk-foundation' ) }"
					style="border:none; outline:none; box-shadow:none;"
					title="{ __( 'Search products by title or description', 'gk-foundation' ) }"
				>
				<button
