					type="button"
					on:click={() => setFilter(PRODUCT_FILTER_ALL) }
				>
					<svg class="absolute top-1/2 right-2 transform -translate-y-1/2" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
						<title>
							{ __( 'Clear Search', 'gk-foundation' ) }
						</title>
						<g stroke-linecap="square" stroke-linejoin="miter" stroke-width="2" fill="none" stroke="currentColor" stroke-miterlimit="10">
							<line x1="16" y1="8" x2="8" y2="16"></line>
							<line x1="16" y1="16" x2="8" y2="8"></line>
						</g>
					</svg>
				</button>
			</div>
		{/if}
		<div class="hidden sm:block">
			<div class="bg-white pl-5 border rounded-md mt-6 shadow-sm">
				<nav class="-mb-px flex items-center space-x-5 md:space-x-2 lg:space-x-6" aria-label="{ __( 'Tabs', 'gk-foundation' ) }">
					<button
						type="button"
						on:click={() => setFilter(PRODUCT_FILTER_ALL) }
						class="{activeFilter === PRODUCT_FILTER_ALL ? 'border-blue-gv text-gray-900 font-bold' : 'border-transparent text-gray-500 hover:text-gray-900 font-medium'} whitespace-nowrap py-3.5 border-b-4 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
						aria-current={activeFilter === PRODUCT_FILTER_ALL ? 'this' : undefined}
					>
						{ __( 'All', 'gk-foundation' ) }
						<span class="{activeFilter === PRODUCT_FILTER_ALL ? 'bg-blue-gv text-white' : 'bg-gray-100 text-gray-900'} hidden ml-3 py-0.5 px-2.5 rounded-full text-xs font-medium md:inline-block">
							{productCount[ PRODUCT_FILTER_ALL ]}
						</span>
					</button>

					{#if permissions.manage_licenses && productCount[ PRODUCT_FILTER_UNLICENSED ]}
						<button
							type="button"
							on:click={() => setFilter(PRODUCT_FILTER_UNLICENSED) }
							class="{activeFilter === PRODUCT_FILTER_UNLICENSED ? 'border-blue-gv text-red-900 font-bold' : 'border-transparent text-red-500 hover:text-red-900 font-medium'} whitespace-nowrap py-3.5 border-b-4 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
							aria-current={activeFilter === PRODUCT_FILTER_UNLICENSED ? 'this' : undefined}
						>
							{ __( 'Unlicensed', 'gk-foundation' ) }
							<span class="{activeFilter === PRODUCT_FILTER_UNLICENSED ? 'bg-blue-gv text-white' : 'bg-red-100 text-red-900'} hidden ml-3 py-0.5 px-2.5 rounded-full text-xs font-medium md:inline-block">
								{productCount[ PRODUCT_FILTER_UNLICENSED ]}
							</span>
						</button>
					{/if}

					{#if permissions.install_products && productCount[ PRODUCT_FILTER_UPDATE_AVAILABLE ]}
						<button
							type="button"
							on:click={() => setFilter(PRODUCT_FILTER_UPDATE_AVAILABLE) }
							class="{activeFilter === PRODUCT_FILTER_UPDATE_AVAILABLE ? 'border-blue-gv text-red-900 font-bold' : 'border-transparent text-red-500 hover:text-red-900 font-medium'} whitespace-nowrap py-3.5 border-b-4 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
							aria-current={activeFilter === PRODUCT_FILTER_UPDATE_AVAILABLE ? 'this' : undefined}
						>
							{ __( 'Update Available', 'gk-foundation' ) }
							<span class="{activeFilter === PRODUCT_FILTER_UPDATE_AVAILABLE ? 'bg-blue-gv text-white' : 'bg-red-100 text-red-900'} hidden ml-3 py-0.5 px-2.5 rounded-full text-xs font-medium md:inline-block">
								{productCount[ PRODUCT_FILTER_UPDATE_AVAILABLE ]}
							</span>
						</button>
					{/if}

					<button
						type="button"
						on:click={() => setFilter(PRODUCT_FILTER_ACTIVE) }
						class="{activeFilter === PRODUCT_FILTER_ACTIVE ? 'border-blue-gv text-gray-900 font-bold' : 'border-transparent text-gray-500 hover:text-gray-900 font-medium'} whitespace-nowrap py-3.5 border-b-4 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
						aria-current={activeFilter === PRODUCT_FILTER_ACTIVE ? 'this' : undefined}
					>
						{ __( 'Active', 'gk-foundation' ) }
						<span class="{activeFilter === PRODUCT_FILTER_ACTIVE ? 'bg-blue-gv text-white' : 'bg-gray-100 text-gray-900'} hidden ml-3 py-0.5 px-2.5 rounded-full text-xs font-medium md:inline-block">
							{productCount[ PRODUCT_FILTER_ACTIVE ]}
						</span>
					</button>

					<button
						type="button"
						on:click={() => setFilter(PRODUCT_FILTER_INACTIVE) }
						class="{activeFilter === PRODUCT_FILTER_INACTIVE ? 'border-blue-gv text-gray-900 font-bold' : 'border-transparent text-gray-500 hover:text-gray-900 font-medium'} whitespace-nowrap py-3.5 border-b-4 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
						aria-current={activeFilter === PRODUCT_FILTER_INACTIVE ? 'this' : undefined}
					>
						{ __( 'Inactive', 'gk-foundation' ) }
						<span class="{activeFilter === PRODUCT_FILTER_INACTIVE ? 'bg-blue-gv text-white' : 'bg-gray-100 text-gray-900'} hidden ml-3 py-0.5 px-2.5 rounded-full text-xs font-medium md:inline-block">
							{productCount[ PRODUCT_FILTER_INACTIVE ]}
						</span>
					</button>

					<button
						type="button"
						on:click={() => setFilter(PRODUCT_FILTER_NOT_INSTALLED) }
						class="{activeFilter === PRODUCT_FILTER_NOT_INSTALLED ? 'border-blue-gv text-gray-900 font-bold' : 'border-transparent text-gray-500 hover:text-gray-900 font-medium'} whitespace-nowrap py-3.5 border-b-4 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
						aria-current={activeFilter === PRODUCT_FILTER_NOT_INSTALLED ? 'this' : undefined}
					>
						{ __( 'Not Installed', 'gk-foundation' ) }
						<span class="{activeFilter === PRODUCT_FILTER_NOT_INSTALLED ? 'bg-blue-gv text-white' : 'bg-gray-100 text-gray-900'} hidden ml-3 py-0.5 px-2.5 rounded-full text-xs font-medium md:inline-block">
							{productCount[ PRODUCT_FILTER_NOT_INSTALLED ]}
						</span>
					</button>

					{#if activeFilter === PRODUCT_FILTER_CUSTOM }
						<div class="h-full w-96 py-2 px-3">
							<div class="relative h-full border border-gray-300 rounded focus-within:outline-none focus-within:ring-2 focus-within:ring-gray-900">
								<svg class="absolute top-1/2 left-2.5 transform -translate-y-1/2" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
									<title>
										{ __( 'Search Products', 'gk-foundation' ) }
									</title>
									<path d="M12.7 11.3C13.6 10.1 14.1 8.7 14.1 7.1C14.1 3.2 11 0 7.1 0C3.2 0 0 3.2 0 7.1C0 11 3.2 14.2 7.1 14.2C8.7 14.2 10.2 13.7 11.3 12.8L14.3 15.8C14.5 16 14.8 16.1 15 16.1C15.2 16.1 15.5 16 15.7 15.8C16.1 15.4 16.1 14.8 15.7 14.4L12.7 11.3ZM7.1 12.1C4.3 12.1 2 9.9 2 7.1C2 4.3 4.3 2 7.1 2C9.9 2 12.2 4.3 12.2 7.1C12.2 9.9 9.9 12.1 7.1 12.1Z" fill="currentColor"></path>
								</svg>
								<input
									aria-label="{ __( 'Search all GravityKit products using a keyword', 'gk-foundation' ) }"
									type="search"
									value={customFilter}
									use:focusElement
									on:keyup={ ( { code, target: {value} } ) => handleCustomFilterInputChange({code, value}) }
									class="w-full pl-9 bg-transparent border-none outline-none shadow-none sm-md:text-sm"
									placeholder="{ __( 'Search products by title or description', 'gk-foundation' ) }"
									style="border:none; outline:none; box-shadow:none;"
									title="{ __( 'Search products by title or description', 'gk-foundation' ) }"
								>
								<button
									class="bg-gradient-to-r from-transparent to-white h-full absolute right-0 top-0 w-20 text-gray-500 rounded hover:text-gray-900"
									type="button"
									on:click={() => setFilter(PRODUCT_FILTER_ALL) }
								>
									<svg class="absolute top-1/2 right-2 transform -translate-y-1/2" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
										<title>
											{ __( 'Clear Search', 'gk-foundation' ) }
										</title>
										<g stroke-linecap="square" stroke-linejoin="miter" stroke-width="2" fill="none" stroke="currentColor" stroke-miterlimit="10">
											<line x1="16" y1="8" x2="8" y2="16"></line>
											<line x1="16" y1="16" x2="8" y2="8"></line>
										</g>
									</svg>
								</button>
							</div>
						</div>
					{:else}
						<button
							type="button"
							class="border-transparent text-gray-500 hover:text-gray-900 whitespace-nowrap py-3.5 px-3 mt-0.5 border-b-4 font-medium text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
							on:click={() => setFilter(PRODUCT_FILTER_CUSTOM) }
						>
							<svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
								<title>
									{ __( 'Search Products', 'gk-foundation' ) }
								</title>
								<path d="M12.7 11.3C13.6 10.1 14.1 8.7 14.1 7.1C14.1 3.2 11 0 7.1 0C3.2 0 0 3.2 0 7.1C0 11 3.2 14.2 7.1 14.2C8.7 14.2 10.2 13.7 11.3 12.8L14.3 15.8C14.5 16 14.8 16.1 15 16.1C15.2 16.1 15.5 16 15.7 15.8C16.1 15.4 16.1 14.8 15.7 14.4L12.7 11.3ZM7.1 12.1C4.3 12.1 2 9.9 2 7.1C2 4.3 4.3 2 7.1 2C9.9 2 12.2 4.3 12.2 7.1C12.2 9.9 9.9 12.1 7.1 12.1Z" fill="currentColor"></path>
							</svg>
						</button>
					{/if}
				</nav>
			</div>
		</div>
	</div>
{/if}
