<script>
	import { tick } from 'svelte';
	import { fly } from 'svelte/transition';

	import { sanitize, animate, ajaxRequest } from '@/lib/helpers.js';
	import * as notifications from '@/lib/notifications.js';
	import { tooltip } from '@/lib/tooltip.js';

	import Icon from '@/components/Icon.svelte';

	import { products } from '@/stores/products';
	import { app } from '@/stores/app';
	import { licenses } from '@/stores/licenses';

	const { __, _x } = window.gkLicenses;
	const { permissions, isNetworkAdmin, _wpAjaxUrl: wpAjaxUrl, _wpNonce: wpNonce, _wpAjaxAction: wpAjaxAction, ajaxRouter } = $app;

	export let product = {};
	export let placeholder = false;

	let componentActionInProgress = false;
	let elementActionInProgress = null;

	let licenseKey = product.license;
	let licenseKeyInput = false;
	let licenseKeyInputEl;
	let licenseKeyLinkEl;

	let activateDeactivateEl;

	let productCardActionAreaEl;

	/**
	 * Sets focus on the element when it's added to DOM.
	 *
	 * @since 1.0.0
	 *
	 * @param {HTMLElement} el
	 */
	function focusElement( el ) {
		el.focus();
	}

	/**
	 * Cancels input of license key.
	 *
	 * @since 1.0.0
	 *
	 * @param {PointerEvent} event
	 */
	async function cancelLicenseInput( event ) {
		licenseKeyInput = false;

		if ( event?.pointerType === '' ) {
			await tick();

			licenseKeyLinkEl.focus();

		}
	}

	/**
	 * Processes license key input, including especial key presses.
	 *
	 * @since 1.0.0
	 *
	 * @param {Object} obj
	 * @param {Object} obj.code Pressed keyboard code.
	 * @param {Object} obj.value License key.
	 */
	function handleLicenseInputChange( { code, value } ) {
		switch ( code ) {
			case 'Escape':
				return cancelLicenseInput( new PointerEvent( 'pointercancel' ) );
			case 'Enter':
				if ( licenseKey ) {
					return activateLicense();
				}
		}

		licenseKey = value.replace( /[^0-9a-z]/gi, '' );
	}

	/**
	 * Activates license.
	 *
	 * @since 1.0.0
	 */
	function activateLicense() {
		if ( Object.values( $licenses ).filter( license => license.masked_key.match( new RegExp( licenseKey.substring( 0, 4 ) ) ) ).length ) {
			licenseKeyInputEl.focus();

			notifications.warning( __( 'This license is already activated.', 'gk-foundation' ) );

			return;
		}

		queryBackend( {
			ajaxRoute: 'activate_license',
			payload: {
				key: licenseKey
			},
			async callback( { success, response } ) {
				if ( licenseKeyInputEl ) {
					await tick();

					licenseKeyInputEl.focus();
				}

				if ( !success ) {
					notifications.error( response );

					return;
				}

				await updateProducts( {
					matchCondition: ( productData ) => response.products[ productData.id ] && !productData.licenses.includes( response.key ),
					update: ( productData ) => productData.licenses.push( response.key ),
				} );

				$licenses[ response.key ] = response;

				if ( !response.products[ product.id ] ) {
					notifications.warning( __( 'License is not valid for this product but was activated for others.', 'gk-foundation' ) );
				} else {
					notifications.success( __( 'License was activated.', 'gk-foundation' ) );
				}

				licenseKey = null;
				licenseKeyInput = false;
			}
		} );
	}

	/**
	 * Installs product.
	 *
	 * @since 1.0.0
	 */
	function installProduct() {
		elementActionInProgress = event.target.id;

		queryBackend( {
			ajaxRoute: 'install_product',
			payload: {
				id: product.id,
				licenses: product.licenses,
				activate: true
			},
			async callback( { success, response } ) {
				elementActionInProgress = null;

				if ( !success ) {
					notifications.error( response );

					return;
				}

				if ( response.activation_error ) {
					notifications.error( response.activation_error );
				}

				notifications.success( __( 'Product was installed.', 'gk-foundation' ) );

				await updateProducts( {
					matchCondition: ( productData ) => productData.id === product.id,
					update: ( productData ) => {
						productData.installed = true;
						productData.active = response.active;
						productData.network_activated = response.network_activated;
						productData.path = response.path;
					}
				} );
			}
		} );
	}

	/**
	 * Updates product.
	 *
	 * @since 1.0.0
	 *
	 * @param {PointerEvent} event
	 */
	function updateProduct( event ) {
		if ( product.has_git_folder && confirm( __( 'This product is installed from a Git repository. Are you sure you want to update it?', 'gk-foundation' ) ) === false ) {
			return;
		}

		elementActionInProgress = event.target.id;

		queryBackend( {
			ajaxRoute: 'update_product',
			payload: {
				path: product.path
			},
			callback: async ( { success, response } ) => {
				elementActionInProgress = null;

				if ( !success ) {
					notifications.error( response );
				} else {
					notifications.success( __( 'Product was updated.', 'gk-foundation' ) );

					updateProducts( {
						matchCondition: ( productData ) => productData.id === product.id,
						update: ( productData ) => {
							productData.active = response.active;
							productData.network_activated = response.network_activated;
							productData.installed_version = response.installed_version;
							productData.update_available = response.update_available;
						}
					} );
				}
			}
		} );
	}

	/**
	 * Activates or deactivates product.
	 *
	 * @since 1.0.0
	 *
	 * @param {PointerEvent} event
	 */
	async function activateOrDeactivateProduct( event ) {
		elementActionInProgress = event.target.id;

		await queryBackend( {
			ajaxRoute: !product.active ? 'activate_product' : 'deactivate_product',
			payload: {
				path: product.path
			},
			callback: async ( { success, response } ) => {
				elementActionInProgress = null;

				if ( !success ) {
					notifications.error( response );
				} else {
					notifications.success( !product.active ? __( 'Product was activated.', 'gk-foundation' ) : __( 'Product was deactivated.', 'gk-foundation' ) );

					updateProducts( {
						matchCondition: ( productData ) => productData.id === product.id,
						update: ( productData ) => {
							productData.active = response.active;
							productData.network_activated = response.network_activated;
						}
					} );
				}

				if ( event.pointerType === '' ) {
					await ( tick() );

					activateDeactivateEl.focus();
				}
			}
		} );
	}

	/**
	 * Executes a backend action (AJAX request).
	 *
	 * @param {Object} obj - Request data for the AJAX request.
	 * @param {string} obj.ajaxRoute - AJAX route that will be processed in the background.
	 * @param {string} obj.payload - Payload object.
	 * @param {string} obj.callback - Callback to be executed after the AJAX request finishes.
	 *
	 * @since 1.0.0
	 */
	function queryBackend( { ajaxRoute, payload, callback } ) {
		componentActionInProgress = true;

		const request = {
			wpAjaxUrl,
			wpNonce,
			wpAjaxAction,
			ajaxRouter,
			ajaxRoute,
			payload,
		};

		ajaxRequest( request ).then( ( { success, response } ) => {
			componentActionInProgress = false;

			callback( { success, response } );
		} );
	};

	/**
	 * Updates products store.
	 *
	 * @param {Object} obj - Parameters.
	 * @param {function(Object):boolean} obj.matchCondition - Callback to check if a given condition is met.
	 * @param {function(Object)} obj.update - Callback to update products.
	 *
	 * @since 1.0.0
	 */
	function updateProducts( { matchCondition, update } ) {
		const updatedProducts = products.get();

		for ( const sectionData of updatedProducts ) {
			for ( const productData of sectionData.products ) {
				if ( !matchCondition( productData ) ) {
					continue;
				}

				update( productData );
			}
		}

		products.set( updatedProducts );
	}

	$: displayActivateDactivateButtons =
		( product.network_activated && isNetworkAdmin ) ||
		( !product.network_activated && ( product.active && permissions.deactivate_products ) || ( !product.active && permissions.activate_products ) );

	$: if ( productCardActionAreaEl && productCardActionAreaEl.innerHTML.trim() === '' ) {
		productCardActionAreaEl.remove();
	}

	$: if ( isNetworkAdmin && product.active && !product.network_activated ) {
		product.active = false;
	}
</script>

{#if placeholder}
	<!-- Product card skeleton used when product data is fetched -->
	<div class="bg-white border rounded-md w-full shadow-sm flex flex-col justify-between overflow-hidden motion-safe:animate-pulse">
		<div class="flex flex-col h-full space-y-6 lg:flex-row lg:space-y-0 p-5 md:p-8">
			<div class="min-w-[100px]">
				<div class="bg-gray-300 flex h-20 w-20"></div>
			</div>
			<div class="flex flex-col w-full content-between">
				<div class="mb-10">
					<div class="h-4 w-[50%] bg-gray-300"></div>
					<div class="mt-4 h-3 w-[90%] bg-gray-200"></div>
					<div class="mt-2 h-3 w-[100%] bg-gray-200"></div>
					<div class="mt-2 h-3 w-[70%] bg-gray-200"></div>
					<div class="mt-2 h-3 w-[80%] bg-gray-200"></div>
					<div class="mt-2 h-3 w-[90%] bg-gray-200"></div>
					<div class="mt-2 h-3 w-[20%] bg-gray-200"></div>
				</div>
			</div>
		</div>
		<div class="flex bg-gray-50 items-center px-5 py-5 border-t">
			<div class="ml-auto h-8 w-[20%] flex-col bg-gray-300"></div>
		</div>
	</div>
{:else}
	<div
		in:animate="{{ animation: fly, x: -20, duration: 100 }}"
		out:animate="{{ animation: fly, y: -20, duration: 100 }}"
		class="bg-white border rounded-md w-full shadow-sm flex flex-col justify-between overflow-hidden"
	>
		<div class="flex flex-col h-full space-y-6 relative lg:flex-row lg:space-y-0 p-5">
			{#if product.active && product.settings}
				<div class="hidden sm:block absolute right-5 top-5">
					<a
						href="{product.settings}"
						class="cursor-pointer inline-flex items-center space-x-2 bg-white shadow-sm text-sm justify-left font-bold border border-gray-300 overflow-hidden w-[32px] h-[32px] hover:w-[100px] focus:w-[100px] disabled:cursor-not-allowed disabled:hover:bg-white hover:bg-gray-50 hover:text-gray-900 focus:text-gray-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900 rounded-md transition-all"
						tabindex="0"
						dir="rtl"
					>
						<svg class="mx-2 h-4 w-4 min-w-[1rem]" width="17" height="18" viewBox="0 0 17 18" fill="none" xmlns="http://www.w3.org/2000/svg">
							<path d="M17 10.5H14.82C14.65 11.2 14.38 11.85 14.01 12.43L15.55 13.97L13.45 16.07L11.91 14.53C11.33 14.89 10.68 15.16 10 15.32V17.5H7V15.32C6.32 15.16 5.67 14.89 5.09 14.53L3.55 16.07L1.43 13.95L2.97 12.41C2.61 11.83 2.34 11.18 2.18 10.5H0V7.53H2.17C2.33 6.83 2.61 6.18 2.97 5.59L1.43 4.05L3.53 1.95L5.07 3.49C5.65 3.12 6.31 2.85 7 2.68V0.5H10V2.68C10.68 2.84 11.33 3.11 11.91 3.47L13.45 1.93L15.57 4.05L14.03 5.59C14.39 6.18 14.67 6.83 14.83 7.53H17V10.5ZM8.5 12C10.16 12 11.5 10.66 11.5 9C11.5 7.34 10.16 6 8.5 6C6.84 6 5.5 7.34 5.5 9C5.5 10.66 6.84 12 8.5 12Z" fill="currentColor"></path>
						</svg>
						{ __( 'Settings', 'gk-foundation' ) }
						<span class="sr-only">
						{ ' ' + __( 'Go to the settings for this product.', 'gk-foundation' ) }
					</span>
					</a>
				</div>
			{/if}
			<div class="min-w-[100px]">
				<div class="flex h-20 w-20 {!product.icon ? 'bg-blue-400' : ''} rounded-md items-center justify-center">
					{#if product.icon}
						<img src="{product.icon}" alt="{product.title} product cover image" class="border-0">
					{:else}
						<svg width="47" height="47" viewBox="0 0 47 47" fill="none" xmlns="http://www.w3.org/2000/svg">
							<path d="M11.5 5.5V13.5" stroke="white" stroke-width="3" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"></path>
							<path d="M39.5 13.5H45.5" stroke="white" stroke-width="3" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"></path>
							<path d="M1.5 13.5H24.328" stroke="white" stroke-width="3" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"></path>
							<path d="M40.075 5.5H45.5V45.5H1.5V5.5H32.328" stroke="white" stroke-width="3" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"></path>
							<path d="M24.748 21.0.081L20.748 17.081L35.5 2.328C36.605 1.223 38.395 1.223 39.5 2.328C40.605 3.433 40.605 5.223 39.5 6.328L24.748 21.0.081Z" stroke="white" stroke-width="3" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"></path>
							<path d="M13.1502 23.021C14.7122 21.459 17.2452 21.459 18.8072 23.021C20.3692 24.583 20.3692 27.116 18.8072 28.678C17.2452 30.24 11.7362 30.092 11.7362 30.092C11.7362 30.092 11.5882 24.583 13.1502 23.021Z" stroke="white" stroke-width="3" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"></path>
							<path d="M9.5 37.5H37.5" stroke="white" stroke-width="3" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"></path>
						</svg>
					{/if}
				</div>
			</div>
			<div class="flex flex-col w-full content-between">
				<div class="space-y-4">
					<h3 class="text-xl font-bold leading-4">
						{@html sanitize( product.title )}
						<span class="inline-flex text-xs {product.update_available && permissions.install_products ? 'text-red-500' : 'text-gray-500'} font-semibold ml-1">
							{product.installed_version || product.server_version}
						</span>
					</h3>
					{#if product.installed}
						{#if !product.licenses?.length && permissions.manage_licenses}
							<p role="status" class="text-xs text-red-700 inline-flex items-center space-x-1 font-bold uppercase">
								<svg class="inline" width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M7 0C3.15 0 0 3.15 0 7C0 10.85 3.15 14 7 14C10.85 14 14 10.85 14 7C14 3.15 10.85 0 7 0ZM7 10.5C6.475 10.5 6.125 10.15 6.125 9.625C6.125 9.1 6.475 8.75 7 8.75C7.525 8.75 7.875 9.1 7.875 9.625C7.875 10.15 7.525 10.5 7 10.5ZM7.875 7.875H6.125V3.5H7.875V7.875Z" fill="#B00000" />
								</svg>
								<span>
									{ __( 'Unlicensed product', 'gk-foundation' ) }
								</span>
							</p>
						{:else if product.update_available && permissions.install_products}
							<div role="status" class="text-xs text-green-600 inline-flex items-center space-x-1 pl-1.5 pr-2 py-1 font-bold uppercase border-2 border-green-600 rounded-full focus:text-gray-900 focus:border-gray-900">
								<Icon type="infoMark" />
								<span class="inline">
									<span class="uppercase">
										{ product.server_version } { _x( 'Available', 'Used in the "version X is available" context.', 'gk-foundation' ) }
									</span>
								</span>
							</div>
						{/if}
					{/if}
					<p class="text-gray-600 text-sm">
						{@html sanitize( product.excerpt )}
					</p>
					<div class="flex items-center space-x-4">
						<a
							href="{product.link}"
							target="_blank"
							class="gk-link text-sm"
							title="{ _x( 'Learn more about [product_title]', 'Placeholders inside [] are not to be translated.', 'gk-foundation' ).replace('[product_title]', product.title) }"
						>
							{ __( 'Learn More', 'gk-foundation' ) }
							<span class="sr-only">
								{ ' ' + __( 'This link opens in a new window.', 'gk-foundation' ) }
							</span>
						</a>
						{#if product.docs }
							<a
								href="{product.docs}"
								target="_blank"
								class="gk-link text-sm"
								title="{ __( 'View documentation for ' + product.title, 'gk-foundation' ) }"
							>
								{ __( 'Documentation', 'gk-foundation' ) }
								<span class="sr-only">
								{ ' ' + __( 'Open Docs in a new window.', 'gk-foundation' ) }
							</span>
							</a>
						{/if}
					</div>
					{#if permissions.install_products && product.failed_system_requirements}
						<div class="flex items-start space-x-2 p-4 bg-red-700 text-white rounded-md">
							<svg class="mt-0.5" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
								<g clip-path="url(#clip0_728_2300)">
									<path d="M8 0C3.6 0 0 3.6 0 8C0 12.4 3.6 16 8 16C12.4 16 16 12.4 16 8C16 3.6 12.4 0 8 0ZM8 14C4.7 14 2 11.3 2 8C2 4.7 4.7 2 8 2C11.3 2 14 4.7 14 8C14 11.3 11.3 14 8 14Z" fill="white" />
									<path d="M9 4H7V9H9V4Z" fill="currentColor" />
									<path d="M8 12C8.55228 12 9 11.5523 9 11C9 10.4477 8.55228 10 8 10C7.44772 10 7 10.4477 7 11C7 11.5523 7.44772 12 8 12Z" fill="currentColor" />
								</g>
								<defs>
									<clipPath id="clip0_728_2300">
										<rect width="16" height="16" fill="currentColor" />
									</clipPath>
								</defs>
							</svg>
							<div>
								<p>
									{ __( 'This product does not meet the minimum system requirements:', 'gk-foundation' ) }
								</p>
								<ul class="mt-2">
									{#each Object.keys( product.failed_system_requirements ) as requirement_key}
										{@const requirement = product.system_requirements[ requirement_key ]}
										{@const failed_requirement = product.failed_system_requirements[ requirement_key ]}
										<li>
											{ requirement.name } { requirement.version } ({ failed_requirement.version } { _x( 'installed', 'Used in the context of "version x.x is installed".', 'gk-foundation' ) })
										</li>
									{/each}
								</ul>
							</div>
						</div>
					{/if}
					{#if permissions.install_products && product.failed_plugin_dependencies}
						<div class="flex items-start space-x-2 p-4 bg-red-700 text-white rounded-md">
							<svg class="mt-0.5" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
								<g clip-path="url(#clip0_728_2300)">
									<path d="M8 0C3.6 0 0 3.6 0 8C0 12.4 3.6 16 8 16C12.4 16 16 12.4 16 8C16 3.6 12.4 0 8 0ZM8 14C4.7 14 2 11.3 2 8C2 4.7 4.7 2 8 2C11.3 2 14 4.7 14 8C14 11.3 11.3 14 8 14Z" fill="white" />
									<path d="M9 4H7V9H9V4Z" fill="currentColor" />
									<path d="M8 12C8.55228 12 9 11.5523 9 11C9 10.4477 8.55228 10 8 10C7.44772 10 7 10.4477 7 11C7 11.5523 7.44772 12 8 12Z" fill="currentColor" />
								</g>
								<defs>
									<clipPath id="clip0_728_2300">
										<rect width="16" height="16" fill="currentColor" />
									</clipPath>
								</defs>
							</svg>
							<div>
								<p>
									{ __( 'This product requires the following plugins to be installed and activated:', 'gk-foundation' ) }
								</p>
								<ul class="mt-2">
									{#each Object.keys( product.failed_plugin_dependencies ) as dependency_key}
										{@const dependency = product.plugin_dependencies[ dependency_key ]}
										{@const failed_dependency = product.failed_plugin_dependencies[ dependency_key ]}
										<li>
											{#if failed_dependency.version}
												{ dependency.name } { dependency.version } ({ failed_dependency.version } { _x( 'installed', 'Used in the context of "version x.x is installed".', 'gk-foundation' ) })
											{:else}
												{ dependency.name  }
											{/if}
										</li>
									{/each}
								</ul>
							</div>
						</div>
					{/if}
				</div>
			</div>
		</div>
		<div bind:this={productCardActionAreaEl} class="flex flex-col space-y-4 sm:flex-row sm:space-y-0 bg-gray-50 items-center px-5 py-5 border-t justify-between">
			{#if product.installed && !licenseKeyInput}
				{#if product.active && product.settings}
					<a
						href="{product.settings}"
						target="_blank"
						class="sm:hidden inline-flex items-center justify-center px-3 py-3 h-full w-full space-x-2 bg-white shadow-sm text-sm font-bold border border-gray-300 disabled:cursor-not-allowed disabled:hover:bg-white hover:bg-gray-50 hover:text-gray-900 focus:text-gray-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900 rounded-md"
					>
						<svg class="h-4 w-4" width="17" height="18" viewBox="0 0 17 18" fill="none" xmlns="http://www.w3.org/2000/svg">
							<path d="M17 10.5H14.82C14.65 11.2 14.38 11.85 14.01 12.43L15.55 13.97L13.45 16.07L11.91 14.53C11.33 14.89 10.68 15.16 10 15.32V17.5H7V15.32C6.32 15.16 5.67 14.89 5.09 14.53L3.55 16.07L1.43 13.95L2.97 12.41C2.61 11.83 2.34 11.18 2.18 10.5H0V7.53H2.17C2.33 6.83 2.61 6.18 2.97 5.59L1.43 4.05L3.53 1.95L5.07 3.49C5.65 3.12 6.31 2.85 7 2.68V0.5H10V2.68C10.68 2.84 11.33 3.11 11.91 3.47L13.45 1.93L15.57 4.05L14.03 5.59C14.39 6.18 14.67 6.83 14.83 7.53H17V10.5ZM8.5 12C10.16 12 11.5 10.66 11.5 9C11.5 7.34 10.16 6 8.5 6C6.84 6 5.5 7.34 5.5 9C5.5 10.66 6.84 12 8.5 12Z" fill="currentColor"></path>
						</svg>
						<span class="sm:hidden">
							{ __( 'Settings', 'gk-foundation' ) }
						</span>
						<span class="sr-only">
							{ ' ' + __( 'Go to the settings for this product.', 'gk-foundation' ) }
						</span>
					</a>
				{/if}
				{#if !product.licenses?.length}
					{#if permissions.manage_licenses}
						<div>
							<button
								bind:this={licenseKeyLinkEl}
								type="button"
								class="gk-link text-sm"
								title="{ __( 'Enter License Key', 'gk-foundation' ) }"
								on:click={() => licenseKeyInput = true}
							>
								{ __( 'I have a license key', 'gk-foundation' ) }
							</button>
						</div>
						<div class="sm-md:w-auto">
							<a
								href="{product.buy_link}"
								target="_blank"
								class="inline-flex items-center px-3 py-3 border border-gray-300 text-sm leading-4 font-medium rounded-md text-gray-900 bg-white hover:bg-gray-50 hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-100 focus:ring-gray-900 disabled:cursor-auto disabled:opacity-50"
								title="{ _x( 'Buy [product_title]', 'Placeholders inside [] are not to be translated.', 'gk-foundation' ).replace('[product_title]', product.title) }"
							>
							<span class="mr-3 pr-3 border-r border-gray-300 lg:inline-flex">
								{ __( 'Buy Now', 'gk-foundation' ) }
								<span class="sr-only">
									{ ' ' + __( 'This link opens in a new window.', 'gk-foundation' ) }
								</span>
							</span>
								<svg class="lg:inline-flex" width="11" height="12" viewBox="0 0 11 12" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M1.66675 10.1667L10.0001 1.83337M10.0001 1.83337H1.66675M10.0001 1.83337V10.1667" stroke="#1D2327" stroke-width="1.67" stroke-linecap="round" stroke-linejoin="round" />
								</svg>
							</a>
						</div>
					{/if}
				{:else if displayActivateDactivateButtons}
					<div class="w-full sm:w-auto flex flex-col sm:flex-row items-center ml-auto space-y-4 sm:space-y-0 sm:space-x-2">
						{#if product.installed && product.update_available && permissions.install_products}
							<button
								disabled="{componentActionInProgress || undefined}"
								id="{product.id}-update"
								type="button"
								class="w-full sm:w-auto inline-flex items-center justify-center px-3 py-3 border border-gray-300 text-sm leading-4 font-medium rounded-md text-gray-900 bg-white disabled:cursor-not-allowed disabled:hover:bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900"
								on:click={(e) => updateProduct(e)}
							>
								{#if elementActionInProgress === `${ product.id }-update`}
									<span role="status">
										<Icon type="loadingIndicator" />
									</span>
								{:else}
									<svg class="mr-2 text-green-600" width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
										<g clip-path="url(#clip0_318_99)">
											<path d="M0 7C0 10.85 3.15 14 7 14C10.85 14 14 10.85 14 7C14 3.15 10.85 0 7 0C3.15 0 0 3.15 0 7ZM10.5 7H7.875V10.5H6.125V7H3.5L7 3.5L10.5 7Z" fill="currentColor" />
										</g>
										<defs>
											<clipPath id="clip0_318_99">
												<rect width="16" height="16" fill="white" />
											</clipPath>
										</defs>
									</svg>
								{/if}
								{ __( 'Update', 'gk-foundation' ) }
							</button>
						{/if}
						<button
							bind:this={activateDeactivateEl}
							id="{product.id}-activate-deactivate"
							disabled="{componentActionInProgress || (!product.active && (product.failed_system_requirements || product.failed_plugin_dependencies)) || undefined}"
							type="button"
							role="switch"
							aria-checked="{product.active}"
							class="w-full sm:w-auto inline-flex items-center justify-center px-3 py-3 border border-gray-300 text-sm leading-4 font-medium rounded-md text-gray-900 bg-white disabled:cursor-not-allowed disabled:hover:bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900"
							on:click={(e) =>{ activateOrDeactivateProduct(e)}}
						>
							{#if elementActionInProgress === `${ product.id }-activate-deactivate`}
								<span role="status">
									<Icon type="loadingIndicator" />
								</span>
							{:else}
								<span
									class="{product.active ? 'bg-green-600' : 'bg-gray-600'} relative inline-flex flex-shrink-0 h-4 w-6 mr-2 border-2 border-transparent rounded-full cursor-pointer transition-colors ease-in-out duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900"
								>
									<span class="sr-only">
										{ __( 'Activate/Deactivate', 'gk-foundation' ) }
									</span>
									<span
										aria-hidden="true"
										class="{product.active ? 'translate-x-2.5' : 'translate-x-0.5' } translate-y-0.5 pointer-events-none inline-block h-2 w-2 rounded-full bg-white shadow transform ring-0 transition ease-in-out duration-200"
									>
									</span>
								</span>
							{/if}
							{#if product.active}
								{isNetworkAdmin ? __( 'Network Deactivate', 'gk-foundation' ) : __( 'Deactivate', 'gk-foundation' )}
							{:else}
								{isNetworkAdmin ? __( 'Network Activate', 'gk-foundation' ) : __( 'Activate', 'gk-foundation' )}
							{/if}
						</button>
					</div>
				{/if}
			{:else if !product.installed && product.licenses?.length && permissions.install_products}
				<div class="ml-auto w-full sm-md:w-auto">
					<button
						disabled="{componentActionInProgress || product.failed_system_requirements || product.failed_plugin_dependencies || undefined}"
						id="{product.id}-install"
						type="button"
						class="ml-automr-2 ml-auto inline-flex items-center px-3 py-3 w-full justify-center border border-gray-300 text-sm leading-4 font-medium rounded-md text-gray-900 bg-white disabled:cursor-not-allowed disabled:hover:bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900"
						on:click={(e) =>{ installProduct(e)}}
					>
						{#if elementActionInProgress === `${ product.id }-install`}
							<span role="status">
								<Icon type="loadingIndicator" />
							</span>
						{/if}
						{ __( 'Install Now', 'gk-foundation' ) }
					</button>
				</div>
			{:else if permissions.manage_licenses}
				{#if licenseKeyInput}
					<div class="h-full w-full sm-md:mr-4">
						<div class="relative rounded-md h-full">
							<div class="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
								<svg class="h-4 w-4 text-gray-400" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path
										fill-rule="evenodd" clip-rule="evenodd" d="M13 0C13.2652 5.66374e-05 13.5195 0.105451 13.707 0.293L15.707 2.293C15.8946 2.48049 15.9999 2.73481 16 3V8C15.9999 8.26519 15.8946 8.51951 15.707 8.707L13.707 10.707C13.5195 10.8945 13.2652 10.9999 13 11H9.414L4.707 15.707C4.51951 15.8946 4.26519 15.9999 4 16H1C0.734784 16 0.48043 15.8946 0.292893 15.7071C0.105357 15.5196 0 15.2652 0 15V12C5.66374e-05 11.7348 0.105451 11.4805 0.293 11.293L5 6.586V3C5.00006 2.73481 5.10545 2.48049 5.293 2.293L7.293 0.293C7.48049 0.105451 7.73481 5.66374e-05 8 0H13ZM12.586 2H8.414L7 3.414V7C6.99994 7.26519 6.89455 7.51951 6.707 7.707L2 12.414V14H3.586L8.293 9.293C8.48049 9.10545 8.73481 9.00006 9 9H12.586L14 7.586V3.414L12.586 2ZM11.5 3C11.8978 3 12.2794 3.15804 12.5607 3.43934C12.842 3.72064 13 4.10218 13 4.5C13 4.89782 12.842 5.27936 12.5607 5.56066C12.2794 5.84196 11.8978 6 11.5 6C11.1022 6 10.7206 5.84196 10.4393 5.56066C10.158 5.27936 10 4.89782 10 4.5C10 4.10218 10.158 3.72064 10.4393 3.43934C10.7206 3.15804 11.1022 3 11.5 3Z" fill="currentColor"
									></path>
								</svg>
							</div>
							<input
								bind:this={licenseKeyInputEl}
								bind:value={licenseKey}
								disabled="{componentActionInProgress || undefined}"
								class="focus:outline-none focus:ring-2 focus:ring-gray-900 block py-3 sm:h-full w-full pl-10 sm-md:text-sm border border-gray-300 rounded-md"
								placeholder="{ __( 'Enter your license key', 'gk-foundation' ) }"
								use:focusElement
								on:keyup={ ( { code, target: {value} } ) => handleLicenseInputChange({code, value}) }
							>
						</div>
					</div>
					<div class="flex w-full sm-md:w-auto">
						<button
							disabled="{componentActionInProgress || !licenseKey || undefined}"
							type="button"
							class="mr-2 inline-flex items-center px-3 py-3 w-full justify-center border border-gray-300 text-sm leading-4 font-medium rounded-md text-gray-900 bg-white disabled:cursor-not-allowed disabled:hover:bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900"
							on:click={activateLicense}
						>
							{#if componentActionInProgress}
							<span role="status">
								<Icon type="loadingIndicator" />
							</span>
							{:else}
								<svg class="mr-1" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M8 0C3.589 0 0 3.589 0 8C0 12.411 3.589 16 8 16C12.411 16 16 12.411 16 8C16 3.589 12.411 0 8 0ZM8 14C4.691 14 2 11.309 2 8C2 4.691 4.691 2 8 2C11.309 2 14 4.691 14 8C14 11.309 11.309 14 8 14Z" fill="currentColor"></path>
									<path d="M6.99994 11.4141L3.58594 8.00006L4.99994 6.58606L6.99994 8.58606L10.9999 4.58606L12.4139 6.00006L6.99994 11.4141Z" fill="currentColor"></path>
								</svg>
							{/if}
							{ __( 'Activate', 'gk-foundation' ) }
						</button>
						{#if !componentActionInProgress}
							<button
								disabled="{componentActionInProgress || undefined}"
								type="button"
								class="inline-flex items-center px-3 py-3 w-full justify-center border border-gray-300 text-sm leading-4 font-medium rounded-md text-gray-900 bg-white disabled:cursor-not-allowed disabled:hover:bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900"
								on:click={(e) => cancelLicenseInput(e)}
							>
								{ __( 'Cancel', 'gk-foundation' ) }
							</button>
						{/if}
					</div>
				{:else}
					<div>
						<button
							bind:this={licenseKeyLinkEl}
							type="button"
							class="gk-link text-sm"
							title="{ __( 'Enter License Key', 'gk-foundation' ) }"
							on:click={() => licenseKeyInput = true}
						>
							{ __( 'I have a license key', 'gk-foundation' ) }
						</button>
					</div>
					<div class="sm-md:w-auto">
						<a
							href="{product.buy_link}"
							target="_blank"
							class="inline-flex items-center px-3 py-3 border border-gray-300 text-sm leading-4 font-medium rounded-md text-gray-900 bg-white hover:bg-gray-50 hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-100 focus:ring-gray-900 disabled:cursor-auto disabled:opacity-50"
							title="{ _x( 'Buy [product_title]', 'Placeholders inside [] are not to be translated.', 'gk-foundation' ).replace('[product_title]', product.title) }"
						>
						<span class="mr-3 pr-3 border-r border-gray-300 lg:inline-flex">
							{ __( 'Buy Now', 'gk-foundation' ) }
							<span class="sr-only">
								{ ' ' + __( 'This link opens in a new window.', 'gk-foundation' ) }
							</span>
						</span>
							<svg class="lg:inline-flex" width="11" height="12" viewBox="0 0 11 12" fill="none" xmlns="http://www.w3.org/2000/svg">
								<path d="M1.66675 10.1667L10.0001 1.83337M10.0001 1.83337H1.66675M10.0001 1.83337V10.1667" stroke="#1D2327" stroke-width="1.67" stroke-linecap="round" stroke-linejoin="round" />
							</svg>
						</a>
					</div>
				{/if}
			{/if}
			{#if product.network_activated && !isNetworkAdmin}
				<div
					class="ml-auto"
					use:tooltip={{
							content: __('This product has been been network-activated and only the network administrator can change its status', 'gk-foundation')
					}}
				>
					<button
						disabled="true"
						type="button"
						role="switch"
						aria-checked="true"
						class="inline-flex items-center px-3 py-3 border border-gray-300 text-sm leading-4 font-medium rounded-md text-gray-900 bg-white disabled:bg-gray-100 disabled:cursor-not-allowed disabled:hover:bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900"
					>
						<span class="pointer-events-none bg-gray-600 relative inline-flex flex-shrink-0 h-4 w-6 mr-2 border-2 border-transparent rounded-full cursor-pointer transition-colors ease-in-out duration-200">
							<span aria-hidden="true" class="translate-x-2.5 translate-y-0.5 pointer-events-none inline-block h-2 w-2 rounded-full bg-white shadow transform ring-0 transition ease-in-out duration-200" />
						</span>
						{__( 'Network Active', 'gk-foundation' )}
					</button>
				</div>
			{/if}
		</div>
	</div>
{/if}
