import './app.css';
import App from './App.svelte';

document.addEventListener( 'DOMContentLoaded', () => {
	const $el = document.getElementById( 'wpbody' );

	if ( !$el ) {
		console.error( 'GravityKit Licenses could not initialize - target element not found.' );

		return;
	}

	$el.innerHTML = '';

	window.gkLicenses.__ = window.wp?.i18n?.__ || function ( translation ) { return translation; };
	window.gkLicenses._x = window.wp?.i18n?._x || function ( translation ) { return translation; };

	new App( {
		target: $el,
		props: {
			data: ( window.gkLicenses || {} ).data
		}
	} );
} );
