<script>
	import { ajaxRequest } from '@/lib/helpers.js';
	import * as notifications from '@/lib/notifications.js';

	import Icon from '@/components/Icon.svelte';

	import { products } from '@/stores/products';
	import { app } from '@/stores/app';
	import { licenses } from '@/stores/licenses';

	import { tick } from 'svelte';

	const { __ } = window.gkLicenses;
	const { permissions, isNetworkAdmin } = $app;

	let licenseKey;
	let licenseKeyInputDiv;

	let serverAction;

	let licensesBoxVisible = true;

	let refs = { activateButton: {}, deactivateButtons: {} };

	/**
	 * Processes license key input, including especial key presses.
	 *
	 * @since 1.0.0
	 *
	 * @param {Object} obj
	 * @param {string} obj.code Pressed keyboard code.
	 * @param {string} obj.value License key.
	 */
	function handleLicenseInputChange( { code, value } ) {
		switch ( code ) {
			case 'Enter':
				if ( licenseKey ) {
					return activateLicense();
				}
		}

		licenseKey = value.replace( /[^0-9a-z]/gi, '' );
	}

	/**
	 * Activates license.
	 *
	 * @since 1.0.0
	 */
	function activateLicense() {
		if ( Object.values( $licenses ).filter( license => license.masked_key.match( new RegExp( licenseKey.substring( 0, 4 ) ) ) && license.masked_key.match( new RegExp( licenseKey.slice( -4 ) ) ) ).length ) {
			licenseKeyInputDiv.focus();

			notifications.warning( __( 'This license is already activated.', 'gk-foundation' ) );

			return;
		}

		refs.activateButton.inProgress = true;

		queryBackend( {
			ajaxRoute: 'activate_license',
			payload: {
				key: licenseKey
			},
			async callback( { success, response } ) {
				refs.activateButton.inProgress = false;

				if ( licenseKeyInputDiv ) {
					await tick();

					licenseKeyInputDiv.focus();
				}

				if ( !success ) {
					notifications.error( response );

					return;
				}

				await updateProducts( {
					matchCondition: ( productData ) => response.products[ productData.id ] && !productData.licenses.includes( response.key ),
					update: ( productData ) => productData.licenses.push( response.key ),
				} );

				licenseKey = null;

				$licenses[ response.key ] = response;

				notifications.success( __( 'License was activated.', 'gk-foundation' ) );
			}
		} );
	}

	/**
	 * Deactivates license.
	 *
	 * @since 1.0.0
	 *
	 * @param {string} key License key.
	 * @param {PointerEvent} e Click event.
	 */
	async function deactivateLicense( key, e ) {
		refs.deactivateButtons[ key ].inProgress = true;

		const payload = {
			key
		};

		if ( e?.altKey ) {
			payload.force_removal = true;
		}

		await queryBackend( {
			ajaxRoute: 'deactivate_license',
			payload,
			async callback( { success, response } ) {
				refs.deactivateButtons[ key ].inProgress = false;

				if ( !success ) {
					notifications.error( response );

					return;
				}

				await updateProducts( {
					matchCondition: ( productData ) => productData.licenses.includes( key ),
					update: ( productData ) => productData.licenses = productData.licenses.filter( license => license !== key )
				} );

				licenses.update( _licenses => {
					delete _licenses[ key ];

					return _licenses;
				} );

				notifications.success( __( 'License was deactivated.', 'gk-foundation' ) );
			}
		} );
	}

	/**
	 * Executes a backend action (AJAX request).
	 *
	 * @param {Object} obj - Request data for the AJAX request.
	 * @param {string} obj.ajaxRoute - AJAX route that will be processed in the background.
	 * @param {string} obj.payload - Payload object.
	 * @param {string} obj.callback - Callback to be executed after the AJAX request finishes.
	 *
	 * @since 1.0.0
	 */
	function queryBackend( { ajaxRoute, payload, callback } ) {
		serverAction = true;

		const request = {
			wpAjaxUrl: $app._wpAjaxUrl,
			wpNonce: $app._wpNonce,
			wpAjaxAction: $app._wpAjaxAction,
			ajaxRouter: $app.ajaxRouter,
			ajaxRoute,
			payload,
		};

		ajaxRequest( request ).then( ( { success, response } ) => {
			serverAction = false;

			callback( { success, response } );
		} );
	}

	/**
	 * Updates products store.
	 *
	 * @param {Object} obj - Parameters.
	 * @param {function(Object):boolean} obj.matchCondition - Callback to check if a given condition is met.
	 * @param {function(Object)} obj.update - Callback to update products.
	 *
	 * @since 1.0.0
	 */
	function updateProducts( { matchCondition, update } ) {
		const updatedProducts = products.get();

		for ( const sectionData of updatedProducts ) {
			for ( const productData of sectionData.products ) {
				if ( !matchCondition( productData ) ) {
					continue;
				}

				update( productData );
			}
		}

		products.set( updatedProducts );
	}
</script>

{#if $licenses && Object.values( $licenses ).length}
	<div class="bg-white border rounded-md shadow-sm divide-y overflow-hidden">
