					<path
						fill="currentColor" fill-rule="evenodd" clip-rule="evenodd" d="M26.25 10C26.5815 10.0001 26.8994 10.1318 27.1337 10.3662L29.6338 12.8663C29.8682 13.1006 29.9999 13.4185 30 13.75V20C29.9999 20.3315 29.8682 20.6494 29.6338 20.8837L27.1337 23.3837C26.8994 23.6182 26.5815 23.7499 26.25 23.75H21.7675L15.8837 29.6338C15.6494 29.8682 15.3315 29.9999 15 30H11.25C10.9185 30 10.6005 29.8683 10.3661 29.6339C10.1317 29.3995 10 29.0815 10 28.75V25C10.0001 24.6685 10.1318 24.3506 10.3662 24.1163L16.25 18.2325V13.75C16.2501 13.4185 16.3818 13.1006 16.6163 12.8663L19.1163 10.3662C19.3506 10.1318 19.6685 10.0001 20 10H26.25ZM25.7325 12.5H20.5175L18.75 14.2675V18.75C18.7499 19.0815 18.6182 19.3994 18.3837 19.6337L12.5 25.5175V27.5H14.4825L20.3663 21.6163C20.6006 21.3818 20.9185 21.2501 21.25 21.25H25.7325L27.5 19.4825V14.2675L25.7325 12.5ZM24.375 13.75C24.8723 13.75 25.3492 13.9475 25.7008 14.2992C26.0525 14.6508 26.25 15.1277 26.25 15.625C26.25 16.1223 26.0525 16.5992 25.7008 16.9508C25.3492 17.3025 24.8723 17.5 24.375 17.5C23.8777 17.5 23.4008 17.3025 23.0492 16.9508C22.6975 16.5992 22.5 16.1223 22.5 15.625C22.5 15.1277 22.6975 14.6508 23.0492 14.2992C23.4008 13.9475 23.8777 13.75 24.375 13.75Z"
