							<th scope="col" class="bg-gray-50 py-3 px-3 text-left text-sm font-semibold text-gray-900">
								{ __( 'Status', 'gk-foundation' ) }
							</th>
							<th scope="col" class="bg-gray-50 py-3 px-3 text-left text-sm font-semibold text-gray-900">
								{ __( 'Expiration', 'gk-foundation' ) }
							</th>
							{#if permissions.manage_licenses}
								<th scope="col" class="bg-gray-50 relative py-3 pl-3 pr-4">
									<span class="sr-only">
										{ __( 'Deactivate', 'gk-foundation' ) }
									</span>
								</th>
							{/if}
						</tr>
						</thead>
						<tbody>
						{#each Object.values( $licenses ) as license}
							<tr>
								<td class="whitespace-nowrap py-3 px-3 text-sm font-semibold text-gray-900">
									{license.license_name}
								</td>
								<td class="whitespace-nowrap py-3 pl-4 pr-3 text-sm font-medium text-gray-500">
									{@html license.masked_key.replace( /(!?\W+)/, '<span class="text-gray-200">$1</span>' )}
								</td>
								<td class="whitespace-nowrap py-3 px-3 text-sm text-gray-500">
									{#if license.expired}
										<span class="inline-flex rounded-full bg-red-100 px-2 text-xs font-semibold leading-5 text-red-800">
											{ __( 'Expired', 'gk-foundation' ) }
										</span>
									{:else}
										<span class="inline-flex rounded-full bg-green-100 px-2 text-xs font-semibold leading-5 text-green-800">
											{ __( 'Active', 'gk-foundation' ) }
										</span>
									{/if}
								</td>
								<td class="whitespace-nowrap py-3 px-3 text-sm text-gray-500">
									{#if license.expiry === 'lifetime'}
										{ __( 'Lifetime, never expires!', 'gk-foundation' ) }
									{:else}
										{license.expiry}
									{/if}
								</td>
								{#if permissions.manage_licenses}
									<td class="relative whitespace-nowrap py-3 pl-3 pr-4 text-right text-sm font-medium">
