											{ __( 'Hardcoded license.', 'gk-foundation' ) }
										{:else}
											<button
												bind:this={refs.deactivateButtons[license.key]}
												disabled="{refs.deactivateButtons[license.key ]?.inProgress || undefined}"
												on:click={(e) => deactivateLicense(license.key, e)}
												class="gk-link destructive"
												title="{ __( 'Deactivate License', 'gk-foundation' ) }"
											>
												{#if refs.deactivateButtons[ license.key ]?.inProgress }
													<span role="status">
														<Icon type="loadingIndicator" />
													</span>
												{:else}
													{ license.expired ? __( 'Remove', 'gk-foundation' ) : __( 'Deactivate', 'gk-foundation' ) }

													<span class="sr-only">
														{' ' + __( 'key starting with [key]', 'gk-foundation' ).replace( '[key]', license.masked_key.substring( 0, 4 ) ) }
													</span>
												{/if}
											</button>
										{/if}
									</td>
								{/if}
							</tr>
						{/each}
						</tbody>
					</table>
				</div>
			</div>
			{#if permissions.manage_licenses}
				<div class="p-5 bg-gray-50 border-t border-gray-200 flex items-center justify-between space-x-3">
					<div class="w-full">
						<div class="relative rounded">
