								<svg class="h-4 w-4 text-gray-400" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path
										fill-rule="evenodd" clip-rule="evenodd" d="M13 0C13.2652 5.66374e-05 13.5195 0.105451 13.707 0.293L15.707 2.293C15.8946 2.48049 15.9999 2.73481 16 3V8C15.9999 8.26519 15.8946 8.51951 15.707 8.707L13.707 10.707C13.5195 10.8945 13.2652 10.9999 13 11H9.414L4.707 15.707C4.51951 15.8946 4.26519 15.9999 4 16H1C0.734784 16 0.48043 15.8946 0.292893 15.7071C0.105357 15.5196 0 15.2652 0 15V12C5.66374e-05 11.7348 0.105451 11.4805 0.293 11.293L5 6.586V3C5.00006 2.73481 5.10545 2.48049 5.293 2.293L7.293 0.293C7.48049 0.105451 7.73481 5.66374e-05 8 0H13ZM12.586 2H8.414L7 3.414V7C6.99994 7.26519 6.89455 7.51951 6.707 7.707L2 12.414V14H3.586L8.293 9.293C8.48049 9.10545 8.73481 9.00006 9 9H12.586L14 7.586V3.414L12.586 2ZM11.5 3C11.8978 3 12.2794 3.15804 12.5607 3.43934C12.842 3.72064 13 4.10218 13 4.5C13 4.89782 12.842 5.27936 12.5607 5.56066C12.2794 5.84196 11.8978 6 11.5 6C11.1022 6 10.7206 5.84196 10.4393 5.56066C10.158 5.27936 10 4.89782 10 4.5C10 4.10218 10.158 3.72064 10.4393 3.43934C10.7206 3.15804 11.1022 3 11.5 3Z" fill="currentColor"
									></path>
								</svg>
							</div>
							<input
