								class="block h-10 w-full p-3 pl-10 sm-md:text-sm border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-gray-900"
							>
						</div>
					</div>
					<button
						bind:this={refs.activateButton}
						disabled="{refs.activateButton?.inProgress || !licenseKey || undefined}"
