							</svg>
							<h3 class="flex flex-col py-2 text-2xl font-bold text-gray-900 w-full sm:w-auto sm:flex-row items-start sm:items-center">
								{ __( 'No Licenses Activated', 'gk-foundation' ) }
							</h3>
							{#if permissions.manage_licenses}
								<a
									href="https://www.gravitykit.com/pricing/"
									target="_blank"
									class="gk-link text-sm w-fit"
									title="{ __( 'Purchase License', 'gk-foundation' ) }"
								>
									{ __( 'Purchase License', 'gk-foundation' ) }
									<span class="sr-only">
									{ ' ' + __( 'This link opens in a new window.', 'gk-foundation' ) }
								</span>
								</a>
							{/if}
						</div>
					</div>
				</div>
				{#if permissions.manage_licenses}
					<div class="flex flex-col items-start space-y-4 lg:w-96">
						<label for="licenseKey" class="flex text-sm text-gray-500 sm:items-center">
							<svg class="mr-1 h-4 w-4" style="min-width: 16px" viewBox="0 0 15 14" fill="none" xmlns="http://www.w3.org/2000/svg">
								<path fill="#9CA2A7" d="M7.5 0C11.3675 0 14.5 3.1325 14.5 7C14.5 10.8675 11.3675 14 7.5 14C3.6325 14 0.5 10.8675 0.5 7C0.5 3.1325 3.6325 0 7.5 0ZM8.375 3.5C8.375 3.01875 7.98125 2.625 7.5 2.625C7.01875 2.625 6.625 3.01875 6.625 3.5C6.625 3.98125 7.01875 4.375 7.5 4.375C7.98125 4.375 8.375 3.98125 8.375 3.5ZM8.375 11.375V6.125H6.625V11.375H8.375Z" />
							</svg>
							<span>
							{ __( 'Enter your license key to activate.', 'gk-foundation' ) }
						</span>
						</label>
						<input
							id="licenseKey"
							bind:this={licenseKeyInputDiv}
							bind:value={licenseKey}
							disabled="{refs.activateButton?.inProgress || undefined}"
							placeholder="{ __( 'Enter your license key', 'gk-foundation' ) }"
							on:keyup={ ( { code, target: {value} } ) => handleLicenseInputChange({code, value}) }
							class="p-2 shadow-sm block w-full sm:text-sm border border-gray-300 rounded-md lg:max-w-lg focus:outline-none focus:ring-2 focus:ring-gray-900"
						>
						<button
							bind:this={refs.activateButton}
							disabled="{refs.activateButton?.inProgress || !licenseKey || undefined}"
							title="{ __( 'Activate License', 'gk-foundation' ) }"
							type="button"
							class="gk-button-primary"
							on:click={activateLicense}
						>
							{#if refs.activateButton?.inProgress}
								<span role="status">
									<Icon type="loadingIndicator" />
								</span>
							{:else}
								<svg class="mr-2" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path fill="currentColor" d="M8 0C3.589 0 0 3.589 0 8C0 12.411 3.589 16 8 16C12.411 16 16 12.411 16 8C16 3.589 12.411 0 8 0ZM8 14C4.691 14 2 11.309 2 8C2 4.691 4.691 2 8 2C11.309 2 14 4.691 14 8C14 11.309 11.309 14 8 14Z" />
									<path fill="currentColor" d="M6.99994 11.4141L3.58594 8.00006L4.99994 6.58606L6.99994 8.58606L10.9999 4.58606L12.4139 6.00006L6.99994 11.4141Z" />
								</svg>
							{/if}
							{ __( 'Activate License', 'gk-foundation' ) }
						</button>
					</div>
				{/if}
			</div>
		</div>
	</div>
{/if}
