<script>
	import { filteredProducts as products } from '@/stores/products';
	import { productFilter } from '@/stores/product-filter';
	import { app } from '@/stores/app';
	import { sanitize } from '@/lib/helpers';

	import Icon from '@/components/Icon.svelte';

	const { __ } = window.gkLicenses;

	import ProductCard from '@/Products/ProductCard.svelte';
	import ProductsFilter from '@/Products/ProductsFilter.svelte';
