import DOMPurify from 'dompurify';
import snarkdown from 'snarkdown';

/**
 * Sanitizes HTML.
 *
 * @param {string} content
 *
 * @returns {string}
 */
function sanitize( content ) {
	return DOMPurify.sanitize( content );
}

/**
 * Optionally sanitizes and converts content from Markdown to HTML.
 *
 * @param {string} content
 * @param {*} safe
 * @param {*} markdown
 *
 * @returns {string}
 */
function sanitizeAndProcessMarkdown( content, safe, markdown ) {
	if ( !safe ) {
		content = DOMPurify.sanitize( content );
	}

	if ( markdown ) {
		content = snarkdown( content );
	}

	return content;
}

/**
 * Detects if client prefers reduced motion.
 *
 * @type {boolean}
 */
const isReducedMotion = window.matchMedia( '(prefers-reduced-motion: reduce)' ).matches;

/**
 * Animates DOM element if `prefers-reduced-motion` CSS media query is not defined.
 *
 * @param {HTMLElement} node
 * @param {Function} easing Svelte's easing.
 * @param {Function} transition Svelte's transition.
 * @param {number} y
 * @param {number} duration
 * @param {number} delay
 *
 * @returns {Object|null}
 */
function animate( node, { animation: transition, easing, y, duration, delay } ) {
	return isReducedMotion ? transition( node, { y, duration, easing, delay } ) : null;
}

/**
 * Queries server by performing an AJAX request.
 *
 * @param {Object} request
 *
 * @returns {Promise<Object>}
 */
async function ajaxRequest( { wpAjaxUrl, wpNonce, wpAjaxAction, ajaxRouter, ajaxRoute, payload, method = 'POST' } ) {
	let success;
	let response;

	const serverRequest = async () => {
		const body = new FormData();

		body.append( 'nonce', wpNonce );
		body.append( 'action', wpAjaxAction );
		body.append( 'ajaxRouter', ajaxRouter );
		body.append( 'ajaxRoute', ajaxRoute );
		body.append( 'payload', JSON.stringify( payload ) );

		const response = await fetch( wpAjaxUrl, {
			method,
			body
		} );

		return response.status >= 400 && response.status < 600 ? response : response.json();
	};

	const unknownError = 'Unknown server error.';

	return new Promise( resolve => serverRequest()
		.then( res => {
			if ( res && res.success ) {
				success = true;
				response = res.data;

				return;
			}

			if ( !res || !res.data ) {
				throw new Error( res.statusText || unknownError );
			}

			throw new Error( res.data || unknownError );
		} )
		.catch( err => {
			success = false;
			response = err.message || 'Unknown server error.';
		} )
		.finally( () => resolve( { success, response } ) )
	);
}

export { ajaxRequest, animate, isReducedMotion, sanitize, sanitizeAndProcessMarkdown, DOMPurify, snarkdown };