<script>
	import { onMount } from 'svelte';

	import LicensesBox from './LicensesBox.svelte';
	import Products from '@/Products/Products.svelte';
	import Icon from '@/components/Icon.svelte';
	import { SvelteToast } from '@zerodevx/svelte-toast';

	import { app } from '@/stores/app';
	import { licenses } from '@/stores/licenses';
	import { products } from '@/stores/products';

	import { ajaxRequest, sanitize } from '@/lib/helpers';

	export let data;

	const { _wpAjaxUrl, _wpAjaxAction, _wpNonce, ajaxRouter, appTitle, isNetworkAdmin, permissions = {}, globalError: startupError, licensesData = {} } = data || {};

	const { __ } = window.gkLicenses;

	let initialized;
	let initializationError;

	let loader;
	let loaderTitle;
	let loaderMessage;

	app.set( { _wpAjaxUrl, _wpAjaxAction, _wpNonce, ajaxRouter, appTitle, isNetworkAdmin, permissions, loader, loaderTitle, loaderMessage, initializationError } );

	licenses.set( licensesData );

	/**
	 * Refreshes licenses and products data.
	 *
	 * @since 1.0.0
	 */
	function refreshData( { skip_cache } = {} ) {
		$app.initializationError = null;
		$app.loader = true;

		if ( permissions.view_licenses && permissions.view_products ) {
			$app.loaderMessage = __( 'Getting license and product information', 'gk-foundation' );
		} else if ( !permissions.view_licenses ) {
			__( 'Getting product information', 'gk-foundation' );
		} else {
			__( 'Getting license information', 'gk-foundation' );
		}

		const request = {
			wpAjaxUrl: $app._wpAjaxUrl,
			wpNonce: $app._wpNonce,
			wpAjaxAction: $app._wpAjaxAction,
			ajaxRouter: $app.ajaxRouter,
			ajaxRoute: 'get_app_data',
			payload: {
				skip_cache,
				is_network_admin: isNetworkAdmin
			},
		};

		ajaxRequest( request ).then( async ( { success, response } ) => {
			$app.loader = false;

			if ( !success ) {
				$app.initializationError = true;

				return;
			}

			products.set( response.products );

			licenses.set( response.licenses );
		} );
	}

	/**
	 * Fetches licenses and products data on load.
	 *
	 * @since 1.0.0
	 */
	onMount( () => {
		if ( !startupError ) {
			refreshData();
		}
	} );
</script>

