import './process.js';
import tippy from 'tippy.js';
import 'tippy.js/dist/tippy.css';

export const tooltip = ( node, params ) => {
	const defaults = {
		arrow: true,
		animation: 'shift-away',
		placement: 'bottom',
		theme: 'tooltip',
		trigger: 'mouseenter focus',
		interactive: true,
		hideOnClick: false,
		...params,
	};

	const tip = tippy( node, params );

	return {
		update: ( newParams ) => {
			tip.setProps( newParams );
		},
		destroy: () => {
			tip.destroy();
		}
	};
};